@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
.page {
  height: 100%;
  word-break: break-word;
}
.page__body {
  min-width: 320px;
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
  background-color: #ffffff;
}
.main-header__container {
  margin: 0 auto;
  width: 280px;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  padding-top: 10px;
  padding-bottom: 15px;
}
.main-header__logo {
  margin-right: 56px;
}
.main-header__logo svg {
  width: 34px;
  height: 38px;
}
.main-header__logo-image--desktop {
  display: none;
}
.main-header__logo-image--tablet {
  display: none;
}
.main-header__logo:hover {
  opacity: 0.8;
}
.main-header__logo:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-header {
    padding-top: 24px;
    padding-bottom: 31px;
    display: flex;
    width: 709px;
    margin: 0 auto;
    justify-content: space-between;
  }
  .main-header__container {
    width: auto;
    padding: 0;
    margin: 0;
  }
  .main-header__logo {
    margin: 0;
  }
  .main-header__logo,
  .main-header__logo svg {
    width: 174px;
    height: 52px;
  }
  .main-header__logo-image--mobile {
    display: none;
  }
  .main-header__logo-image--desktop {
    display: none;
  }
  .main-header__logo-image--tablet {
    display: block;
  }
}
@media (min-width: 1440px) {
  .main-header {
    padding-top: 53px;
    padding-bottom: 29px;
    width: 1220px;
  }
  .main-header__logo,
  .main-header__logo svg {
    width: 202px;
    height: 59px;
  }
  .main-header__logo-image--mobile {
    display: none;
  }
  .main-header__logo-image--desktop {
    display: block;
  }
  .main-header__logo-image--tablet {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav__toggle-container {
    width: 280px;
    margin: 0 auto;
    margin-bottom: 25px;
    margin-top: -43px;
    display: flex;
    justify-content: flex-end;
  }
  .main-nav--closed .main-nav__toggle {
    border: none;
    background: none;
    background-image: url("../img/icon-hamburger.svg");
    background-size: 24px 18px;
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 18px;
    cursor: pointer;
    display: block;
  }
  .main-nav--opened .main-nav__toggle {
    border: none;
    background: none;
    background-image: url("../img/icon-cross.svg");
    background-size: 18px 18px;
    background-position: center;
    background-repeat: no-repeat;
    width: 24px;
    height: 18px;
    cursor: pointer;
    display: block;
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--opened .main-nav__list {
    position: absolute;
    left: 0;
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
    min-width: 320px;
    display: flex;
    flex-direction: column;
    z-index: 5;
  }
  .main-nav--nojs .main-nav__toggle-container {
    margin-top: -31px;
  }
  .main-nav--nojs .main-nav__toggle {
    visibility: hidden;
  }
  .main-nav--nojs .main-nav__list {
    position: relative;
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: 100%;
    min-width: 320px;
    display: flex;
    flex-direction: column;
  }
}
.main-nav__item {
  text-align: center;
  border-bottom: 1px solid #e6e6e6;
  background-color: #ffffff;
}
.main-nav__item:first-child {
  border-top: 1px solid #e6e6e6;
}
.main-nav__link {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  text-transform: uppercase;
  text-decoration: none;
  display: inline-block;
  padding-top: 22px;
  padding-bottom: 24px;
  width: 100%;
}
.main-nav__link:hover {
  opacity: 0.6;
}
.main-nav__link:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .main-nav__toggle-container,
  .main-nav__toggle {
    display: none;
  }
  .main-nav__list {
    position: relative;
    list-style-type: none;
    padding: 0;
    margin: 0;
    width: auto;
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    flex-wrap: wrap;
  }
  .main-nav__item,
  .main-nav__item:first-child {
    border: none;
    background: none;
  }
  .main-nav__item:not(:first-child) {
    margin-left: 35px;
  }
  .main-nav__link {
    font-family: "Oswald", "Arial Narrow", sans-serif;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    padding-top: 16px;
    padding-bottom: 8px;
  }
  .main-nav__link--current {
    pointer-events: none;
    border-bottom: 2px solid #68B738;
  }
}
@media (min-width: 1440px) {
  .main-nav__item:not(:first-child) {
    margin-left: 37px;
  }
  .main-nav__link {
    font-family: "Oswald", "Arial Narrow", sans-serif;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }
  .main-nav__link--current {
    border-bottom: 2px solid #68B738;
  }
  .main-nav__link--modified {
    color: #ffffff;
    border-color: #ffffff;
  }
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.logo-words {
  width: 101px;
  height: 18px;
}
.logo-words:hover {
  opacity: 0.8;
}
.logo-words:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .logo-words {
    display: none;
  }
}
.product-intro {
  background: linear-gradient(to bottom, #68B738 302px, #ffffff 302px);
}
.product-intro__container {
  width: 320px;
  margin: 0 auto;
  background-image: url("../img/cat-background-green.jpg");
  background-position: top;
  background-size: 320px 302px;
  background-repeat: no-repeat;
  padding-top: 27px;
  padding-bottom: 20px;
}
