  .product-intro__container {
    background-image: url("../img/cat-background-green@2x.jpg");
  }
}
.product-intro__container-inner {
  margin: 0 auto;
  width: 280px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.product-intro__title {
  font-family: "Oswald", "Arial Narrow", sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  color: #ffffff;
  margin: 0;
  margin-bottom: 25px;
  text-align: center;
}
.product-intro__moto {
