.manual__container {
  margin: 0 auto;
  width: 280px;
  padding-bottom: 29px;
}
.manual-title {
  font-size: 36px;
  line-height: 40px;
  margin-top: 0;
  margin-bottom: 40px;
}
.manual__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr;
  row-gap: 20px;
  margin-bottom: 20px;
}
.manual__item {
  display: flex;
  align-items: center;
}
.manual__description {
  margin: 0;
  display: inline-block;
  max-width: 198px;
}
.manual__icon {
  width: 60px;
  height: 60px;
  background-repeat: no-repeat;
  background-position: center;
  margin-right: 20px;
}
.manual__icon--leaf {
  background-image: url("../img/icon-leaf.svg");
}
.manual__icon--boiling {
  background-image: url("../img/icon-boiling.svg");
}
.manual__icon--diet {
  background-image: url("../img/icon-diet.svg");
}
.manual__icon--clock {
  background-image: url("../img/icon-clock.svg");
}
.manual__item-number {
  display: none;
}
