    row-gap: 37px;
    margin-bottom: 0;
  }
  .manual__item {
    flex-direction: column;
    align-items: flex-start;
    padding-top: 40px;
    padding-bottom: 57px;
    position: relative;
    justify-self: start;
  }
  .manual__description {
    font-size: 16px;
    line-height: 24px;
    max-width: 245px;
  }
  .manual__icon {
    width: 80px;
    height: 80px;
    margin-right: 0;
    margin-bottom: 31px;
  }
  .manual__item-number {
    display: inline-block;
    position: absolute;
    right: 0;
    bottom: 0;
    font-family: "Oswald", "Arial Narrow", sans-serif;
    font-weight: 400;
    font-size: 280px;
    line-height: 280px;
    color: #f2f2f2;
    z-index: -1;
  }
  .manual__item:first-child .manual__item-number {
